import { watch, reactive } from 'vue'
export default function (data, text) {
  const ErrData = reactive({
    ErrText: null,
  })
  const RegName = /^[\u4e00-\u9fa5A-Za-z0-9_-~\·]{3,8}$/
  const RegText = /^(?:[\u4e00-\u9fa5\w\s\u3000-\uffff]{3,15}|[a-zA-Z0-9\s\u3000-\uffff]{3,15})$/
  const vUserReg = {
    mounted(el) {
      watch(text, (newText) => {
        const errMsg = data.num === 2 ? '点击图标更改, 仅支持中英文及 _ - ~ ·' : '点击图标更改, 仅支持中日英文及个别符号'
        const errTest = data.num === 2 ? RegName.test(newText) : RegText.test(newText)
        el.style.boxShadow = errTest ? '0px -10px 10px -4px rgb(132 236 110 / 30%) inset' : '0px -10px 10px -4px #f77575 inset'
        ErrData.ErrText = errTest ? null : errMsg
        if (!text.value) {
          el.style.boxShadow = '0px -10px 10px -4px rgb(132 236 110 / 30%) inset'
          ErrData.ErrText = null
        }
      })
    },
  }
  return { vUserReg, ErrData }
}
