import { reactive, onMounted } from 'vue'
export default function (ball, file,text) {
  const UserMap = new Map([
    ['修改头像',(data, event) => Change_Something(data, event, 1)],
    ['修改名字',(data, event) => Change_Something(data, event, 2)],
    ['修改签名',(data, event) => Change_Something(data, event, 3)]
  ])
  
  let stack = []
  function Change_Something(data, event, ID) {
    //设置图标
    !stack[0] ? (data.num = ID) : null
    if (ID === 1 && event) {
      //这里是点击用户上传那里的 input
      data.file_element.click()
    } else if (ID > 1 && event) {
      data.onclick = false
      data.input = true
      if (stack[0] === ID) {
        data.onclick = true
        data.input = false
        //这里的text仅用于将 input 重置
        text.value=""
        stack = []
      } else {
        stack.push(ID)
      }
    }
  }
  const data = reactive({
    flag: false,
    ball_element: null,
    file_element: null,
    num: 0,
    onclick: true,
    input: false,
  })

  function Change_Some(type, flag = false, event) {
    data.flag = flag
    UserMap.has(type) && UserMap.get(type)(data, event)
  }

  onMounted(() => {
    data.ball_element = ball.value
    data.file_element = file.value
  })

  return { data, Change_Some }
}
